import {JSONCrush} from './JSONCrush.js'
import {randomId} from './utils.js'

export default {
  methods: {
    removeNotebook (id) {
      if (!window.confirm('Confirm remove?')) return
      if (!window.confirm('Are you sure?')) return
      this.db.get(id).then( (doc) => {
        return this.db.remove(doc);
      }).then(() => {
        this.getDocsRows()
      })
    },
    getDocsRows() {
      this.db.allDocs({include_docs: true}).then((docs) => {
        this.searchData = docs.rows
        //console.log(docs)
      }).catch((err) => {
        console.log(err)
      })
    },
    saveNotebook () {
      // document id have the following format:
      //nb_<random_id>_<timestamp>
      
      this.notebook._id = this.notebook._id || `nb_${randomId()}_${new Date().toISOString()}`
      let id = this.notebook._id
      //let idPrefix = id.split('_').slice(0,2).join('_') + '_'

      // update
      if (this.notebook._rev) {
        // fetch 
        this.db.get(id).then((doc) => {
          this.notebook._rev = doc._rev
          return this.db.put(this.notebook)
        }).then(() => {
          return this.db.get(id)
        }).then(() => {
          //console.log(doc)
        })

      // create
      } else {
        this.db.put(this.notebook).then(() => {
          return this.db.get(id)
        }).then((doc) => {
          console.log(doc)
          this.notebook._rev = doc._rev
        })
      }
    },
    loadData (event) {
      if (!event.target.files.length) return

      event.target.files[0].text().then((txt) => {
        if (this.notebook.cells.length > 0) {
          if (!window.confirm('This will overwrite your current Notebook. Are you sure?')) return
        }
        // TODO: validate schema
        this.notebook = JSON.parse(txt)
        this.setHistogramAll()
        this.activeTab = 'calc'
        event.target.value = ''
      })
    },
    share (event) {
      const shareData = {
        title: 'MDN',
        text: 'Uncertainty Notebook',
        url: this.permalink
      }

      const btn = event.target
      console.log(btn)

      // Must be triggered some kind of "user activation"
      btn.addEventListener('click', async () => {
        try {
          await navigator.share(shareData)
          
        } catch(err) {
          console.log(err)
          //resultPara.textContent = 'Error: ' + err
        }
      })
    },
    downloadJson () {
      let notebookStr = JSON.stringify(this.notebook, 0, 2)
      var dataStr = 'data:text/json;charset=UTF-8,' + '\uFEFF' + encodeURIComponent(notebookStr)
      var downloadAnchorNode = document.createElement('a')
      downloadAnchorNode.setAttribute("href",     dataStr)
      downloadAnchorNode.setAttribute("download", 'notebook.json')
      document.body.appendChild(downloadAnchorNode) // required for firefox
      downloadAnchorNode.click()
      downloadAnchorNode.remove()
    },
    genPermalink () {
      this.permalink = window.location.origin + window.location.pathname + '#/' + JSONCrush(JSON.stringify(this.notebook))
      this.$nextTick(() => {
        this.$refs.permalinkHref.click()
      })
    },
    // copyPermalink (event) {
    //   event.preventDefault()
    //   this.$refs.permalink.select()
    //   console.log(this.$refs.permalink)
    //   document.execCommand("copy")
    // }
  },
  computed: {
    searchDataFiltered () {
      return this.searchData
        .filter(x => {
          return this.searchFilter ? x.doc.desc && x.doc.desc.includes(this.searchFilter) : true
      })
    }
  }
}