import constants from './constants.js';
import NumberInput from './components/NumberInput.vue'
// import DateInput from './components/DateInput.vue'
import flatPickr from 'vue-flatpickr-component'
import 'flatpickr/dist/themes/dark.css'

import MC from './mc.js';

import filters from './filters.js'
import tags from './tags.js'
import histogram from './histogram.js'
import dataIO from './dataIO.js'
import interactions from './interactions.js'
import information from './information.js'
import charts from './charts.js'

import feather from 'feather-icons'
import {JSONUncrush} from './JSONCrush.js'
import {randomId} from './utils.js'

import PouchDB from 'pouchdb'

export default {
  name: 'App',
  components: {
    NumberInput,
    flatPickr
  }, 
  watch: {
    'modals.search': {
      handler (isOpen, wasOpen) {
        if (isOpen && !wasOpen) {
          this.getDocsRows()
        }
      }      
    },
    // update trend charts trigger
    'filteredData': {
      handler () {
        // net tick to wait for `preFill` call
        this.$nextTick(() => {
          let replaceDatasets = true
          this.setTrendChart(replaceDatasets)
        })
      }
    },
    'chartType': {
      handler () {
        // net tick to wait for `preFill` call
        this.$nextTick(() => {
          let replaceDatasets = true
          this.setTrendChart(replaceDatasets)
        })
      }
    },
    'chartSelectedVar': {
      handler () {
        let replaceDatasets = true
        this.setTrendChart(replaceDatasets)
      }
    },
    'chartTrendCellId': {
      handler () {
        this.$nextTick(() => {
          let replaceDatasets = true
          this.setTrendChart(replaceDatasets)
        })
      }
    },
    'trendChartCells': {
      handler () {
        this.$nextTick(() => {
          let replaceDatasets = true
          this.setTrendChart(replaceDatasets)
        })
      }
    },
    'stateControl.processing': {
      handler (val, prevVal) {
        if (val === false && prevVal === true) {
          this.setTrendChart()
        }
      },
    }
    // notebook: {
    //   handler (val) { // eslint-disable-line no-unused-vars
    //     this.preFill()
    //   },
    //   deep: false
    // },
  },
  data () {
    const firstWbId = randomId()
    return {
      notebook: {
        cells: [],
        currentWb: firstWbId,
        wbs: [Object.assign({}, constants.WB, {id: firstWbId, date: (new Date(Date.now())).toISOString().split('T')[0]})],
        queries: []
      },
      histCharts: {},
      stateControl: { processing: true },
      permalink: '',
      filtersCreated: [],
      workerPool: null,
      mc: null,
      trendChart: null,
      chartType: 'None', // None,Range,Trend
      chartSelectedVar: undefined,
      currentPage: 0,
      perPage: 5,
      chartTrendCellId: undefined,
      db: null,
      modals: {
        search: false
      },
      searchData: [],
      searchFilter: '',
      pathPrefix: window.location.origin + window.location.pathname + '#/',
      queryClipboard: null,
      assetsCache: {}
    }
  },

  /*
  methods
  */

  methods: Object.assign({}, filters.methods, tags.methods, histogram.methods, dataIO.methods, interactions.methods, information.methods, charts.methods),

  /*
  computed
  */

  computed: Object.assign({}, filters.computed, tags.computed, dataIO.computed, /*histogram.computed, interactions.computed,*/ information.computed, charts.computed),
  
  mounted () {
    // clear all timeout. Useful when hotreloading
    var tids = window.setTimeout(function() {}, 0);
    while (tids--) {
      window.clearTimeout(tids); // will do nothing if no timeout with id is present
    }

    // start db
    this.db = new PouchDB('mcdb')
    
    // data importer EventListener handler 
    this.$refs.loadDataInput.addEventListener('change', this.loadData, false)

    let getString = (window.location.href.split('/#/').slice(1).join('/#/'))
    if (getString.length) {
      try {
        //this.notebook = JSON.parse(decodeURI(json))
        this.notebook = JSON.parse(JSONUncrush(decodeURIComponent(getString)))
        this.loadAssets()
        //console.log(this.notebook)
      } catch (e) {
        if (getString.length < 50) {
          let idPrefix = getString.split('_').slice(0,2).join('_') + '_'

          
          //https://pouchdb.com/2014/05/01/secondary-indexes-have-landed-in-pouchdb.html
          this.db.allDocs({startkey: idPrefix, endkey: idPrefix+'\uffff', descending: false, include_docs: true}).then((docs) => {
            let dbLastDoc = docs.rows[0]
            if (dbLastDoc) {
              this.notebook = dbLastDoc.doc
              this.setHistogramAll()    
              this.setTrendChart()
            } else {
              console.warn('Unable to parse url input and doc get document from db.')
            }
            this.loadAssets()
          }).catch((err) => {
            
            console.log(err)
          })

        }
      }
    }

    // check for versions on db
    if (this.notebook._id) {
      //nb_<random_id>_<timestamp>
      let id = this.notebook._id
      let idPrefix = id.split('_').slice(0,2).join('_') + '_'

      // for now we dont use user revisiond by incrementin the timestamp of id.
      // this logic will handle to pick que most recent revision
      this.db.allDocs({startkey: idPrefix, endkey: idPrefix+'\uffff', descending: false}).then((docs) => {
        let dbLastDoc = docs.rows[0]
        
        if (dbLastDoc.id > id) {
          console.log('db version is the most recent')
        }

        if (dbLastDoc.id < id) {
          console.log('loaded version is the most recent')
        }

        if (dbLastDoc.id === id) {
          console.log('db and loaded versions are equal')
        }

        //this.notebook._rev = doc._rev
        //return this.db.put(this.notebook)
      }).catch(() => {
        //console.log()
      })
    }
    
    // set initial state of charts and data
    this.preFill()
    this.setHistogramAll()
    this.setTrendChart()
    

    // icons
    feather.replace()

    // simulation handler
    this.mc = new MC()
    this.workerPool = this.mc.init()
    
    this.mc.waitReadyStatus(() => this.stateControl.processing = false)


    // check clipboard
    const checkClipboard = () => {
      window.setTimeout(() => {
        if (localStorage.queryClipboard) {
          try {
            this.queryClipboard = JSON.parse(localStorage.queryClipboard)
            //console.log(this.queryClipboard)
          } catch (e) {
            //
          }
        }
        checkClipboard()
      }, 1000)
    }
    checkClipboard()

  }
}
