<template>
  <input 
    v-model="value"
    class="input" 
    ref="input"
    :type="isTouch ? 'number' : 'text'" 
    step="1" 
    @blur="setNumber"
    v-bind:class="error ? 'has-background-danger' : ''"
  >
</template>

<script>
export default {
  name: 'number-input',
  props: ['modelValue'],
  emits: ['update:modelValue'],
  data () {
    return {
      prevValue: '',
      error: false,
      isTouch: false
    }
  },
  mounted () {
    if(window.matchMedia("(pointer: coarse)").matches) {
        // check if is touchscreen. This will set input type as 
        // number, enablint the mobile number input
        this.isTouch = true
    }
  },
  methods: {
    setNumber () {
      let num = parseFloat(this.modelValue)
      if (!isNaN(num)) {
        this.$emit('update:modelValue', num)
      } else {
        this.$emit('update:modelValue', this.modelValue)
      }
    }
  },
  computed: {
    value: {
      get() {
        return this.modelValue
      },
      set(value) {
        if (!valueFmt) {
          this.error = false
          this.$emit('update:modelValue', '')
        }

        let valueFmt = value.replaceAll(',', '.')
          .split('')
          .filter(c => !isNaN(+c) || ['-', '+', '.', 'e', 'E'].indexOf(c) !== -1)
          .join('')

        if (valueFmt === '' || isNaN(+valueFmt)) {
          this.error = true
          
          // edge cases
          
          // let eCount = valueFmt.split('').reduce((a, c) => a + ('e' === c.toLowerCase() ? 1 : 0), 0)

          // let charCount = valueFmt.split('').reduce((a, c) => a + ((isNaN(+c) || ['-', '+', '.'].indexOf(c) !== -1) ? 1 : 0), 0)
          
          // if ((valueFmt.length >= 2) &&  (eCount === 1) && ((charCount-eCount) <= 1)) {
          //   this.prevValue = valueFmt
          //   this.$emit('update:modelValue', valueFmt)
          //   return
          // }


          // if (this.$refs.input.value !== this.prevValue) {
          //   this.$refs.input.value = this.prevValue
          // }
          // this.$emit('update:modelValue', this.prevValue)
          // return
        }
        if (value !== valueFmt) {
          this.$refs.input.value = valueFmt
        }
        //this.prevValue = valueFmt
        this.$emit('update:modelValue', valueFmt)
      }
    }
  }
}
</script>

<style scoped>
  /* Chrome, Safari, Edge, Opera */
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  /* Firefox */
  input[type=number] {
    -moz-appearance: textfield;
  }
</style>